
@charset "UTF-8";
/*
 * 基础色
 */
.cat-toast {
  position: fixed;
  top: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 3px;
  color: #fff;
  letter-spacing: -.2em;
  box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  z-index: 99999;
}
.cat-toast .cat-toast-content {
    display: inline-block;
    padding: 8px 15px;
    letter-spacing: 0;
}
.cat-toast .cat-icon {
    display: inline-block;
    color: #fff;
    height: 100%;
    margin-left: 10px;
}
.cat-toast-default {
    background-color: #666;
}
.cat-toast-primary {
    background-color: #3da8f5;
}
.cat-toast-error {
    background-color: #d71345;
}
.cat-toast-warning {
    background-color: #ffaf38;
}
.cat-toast-success {
    background-color: #00B281;
}



