
@charset "UTF-8";
/*
 * 基础色
 */
.view {
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}
.view .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 1;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform .4s ease;
    transition: transform .4s ease;
    transition: transform .4s ease, -webkit-transform .4s ease;
}
.view .sidebar.open-sidebar {
      -webkit-transform: translateX(0);
              transform: translateX(0);
}
.view .open-btn {
    position: fixed;
    top: 20px;
    left: 20px;
    font-size: 14px;
    cursor: pointer;
    padding: 5px 12px;
    border-radius: 20px;
    z-index: 1;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.3);
    transition: -webkit-transform .4s ease;
    transition: transform .4s ease;
    transition: transform .4s ease, -webkit-transform .4s ease;
}
.view .open-btn.open-sidebar {
      -webkit-transform: translateX(280px);
              transform: translateX(280px);
}
.view .main {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    transition: -webkit-transform .4s ease;
    transition: transform .4s ease;
    transition: transform .4s ease, -webkit-transform .4s ease;
}
.view .main.open-sidebar {
      -webkit-transform: translateX(280px);
              transform: translateX(280px);
}



