/*
 * 基础色
 */
.cat-popover-slide-down-enter, .cat-popover-slide-down-leave-to {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  opacity: 0;
}
.cat-popover-slide-down-enter-active, .cat-popover-slide-down-leave-active {
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
}
.cat-popover-slide-up-enter, .cat-popover-slide-up-leave-to {
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  opacity: 0;
}
.cat-popover-slide-up-enter-active, .cat-popover-slide-up-leave-active {
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
}
.cat-popover-slide-left-enter, .cat-popover-slide-left-leave-to {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  opacity: 0;
}
.cat-popover-slide-left-enter-active, .cat-popover-slide-left-leave-active {
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
}
.cat-popover-slide-right-enter, .cat-popover-slide-right-leave-to {
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0;
}
.cat-popover-slide-right-enter-active, .cat-popover-slide-right-leave-active {
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
}
.cat-popover {
  position: absolute;
  z-index: 199;
  background-color: #fff;
  min-width: 180px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.cat-popover-use_shadow {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
}
.cat-popover-left::before {
    content: '';
    position: absolute;
    height: 0;
    width: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 6px;
    border-left-color: inherit;
    left: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.cat-popover-left::after {
    content: '';
    position: absolute;
    height: 0;
    width: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px;
    border-left-color: #fff;
    left: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
