
@charset "UTF-8";
/*
 * 基础色
 */
.cat-modal-fade-enter-active {
  -webkit-animation: fade-in 0.5s;
          animation: fade-in 0.5s;
}
.cat-modal-fade-leave-active {
  -webkit-animation: fade-out 0.5s;
          animation: fade-out 0.5s;
}
@-webkit-keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fade-out {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes fade-out {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.cat-modal-bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.cat-modal-bounce-leave-active {
  -webkit-animation: bounce-out 0.2s;
          animation: bounce-out 0.2s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
}
.cat-modal-slide-down-enter, .cat-modal-slide-down-leave-to {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  opacity: 0;
}
.cat-modal-slide-down-enter-active, .cat-modal-slide-down-leave-active {
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
}
.cat-modal-slide-up-enter, .cat-modal-slide-up-leave-to {
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  opacity: 0;
}
.cat-modal-slide-up-enter-active, .cat-modal-slide-up-leave-active {
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
}
.cat-modal-slide-left-enter, .cat-modal-slide-left-leave-to {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  opacity: 0;
}
.cat-modal-slide-left-enter-active, .cat-modal-slide-left-leave-active {
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
}
.cat-modal-slide-right-enter, .cat-modal-slide-right-leave-to {
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0;
}
.cat-modal-slide-right-enter-active, .cat-modal-slide-right-leave-active {
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
}
.cat-modal-wrap {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.cat-modal-wrap-center {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.cat-modal-wrap-center .cat-modal-close {
      right: 5px;
      top: 5px;
      font-size: 18px;
}
.cat-modal-wrap-top {
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.cat-modal-wrap-top .cat-modal-body {
      width: 100vw;
}
.cat-modal-wrap-top .cat-modal-close {
      right: 5px;
      top: 100%;
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
}
.cat-modal-wrap-bottom {
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
}
.cat-modal-wrap-bottom .cat-modal-body {
      width: 100vw;
}
.cat-modal-wrap-bottom .cat-modal-close {
      right: 5px;
      bottom: 100%;
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
}
.cat-modal-wrap-left {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}
.cat-modal-wrap-left .cat-modal-body {
      height: 100vh;
}
.cat-modal-wrap-left .cat-modal-close {
      left: 100%;
      top: 5px;
      -webkit-transform: translateX(5px);
              transform: translateX(5px);
}
.cat-modal-wrap-right {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
.cat-modal-wrap-right .cat-modal-body {
      height: 100vh;
}
.cat-modal-wrap-right .cat-modal-close {
      right: 100%;
      top: 5px;
      -webkit-transform: translateX(-5px);
              transform: translateX(-5px);
}
.cat-modal-wrap:not(.cat-modal-wrap-center) .cat-modal-close {
    color: #666;
}
.cat-modal-wrap-hasMask:not(.cat-modal-wrap-center) .cat-modal-close {
    color: #fff;
}
.cat-modal-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: table;
  z-index: 1;
}
.cat-modal-mask:hover + .cat-modal .cat-modal-close {
    opacity: 1;
}
.cat-modal {
  position: absolute;
  z-index: 2;
}
.cat-modal .cat-modal-close {
    position: absolute;
    font-size: 18px;
    line-height: 1;
    cursor: pointer;
    opacity: .6;
}
.cat-modal-body {
  width: 100%;
  height: 100%;
  background-color: #fff;
  min-width: 300px;
  padding: 20px;
}
.cat-modal-body-use_shadow {
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.36);
}
.cat-modal-banScroll {
  overflow: hidden;
}
.cat-modal-banScroll body {
    overflow: hidden;
}



