
@charset "UTF-8";
/*
 * 基础色
 */
.cat-input-wrap {
  display: inline-block;
  position: relative;
  margin-top: 24px;
}
.cat-input-label {
  position: absolute;
  top: 50%;
  left: 0px;
  font-size: 14px;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #808080;
  z-index: 1;
  transition: all .3s;
}
.cat-input-label-shrink {
    font-size: 12px;
    top: -15px;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.cat-input {
  width: 100%;
  position: relative;
  padding: 8px 0 4px 0;
  color: #383838;
  background-color: transparent;
  border: none;
  outline: none;
  width: 230px;
  font-size: 14px;
  border-bottom: 2px solid #a6a6a6;
}
.cat-input::-moz-placeholder {
    color: #ccc;
    font-size: 13px;
}
.cat-input:-ms-input-placeholder {
    color: #ccc;
    font-size: 13px;
}
.cat-input::-webkit-input-placeholder {
    color: #ccc;
    font-size: 13px;
}
.cat-input [disabled] {
    opacity: .3;
    cursor: not-allowed;
}
.cat-input:-webkit-autofill {
    -webkit-text-fill-color: #383838;
    -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
.cat-input-progress {
  height: 2px;
  width: 0%;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: width .3s;
}
.cat-input-progress.normal {
    background-color: #8bcbf9;
}
.cat-input-progress.error {
    background-color: #e7718f;
}
.cat-input-progress.success {
    background-color: #66d1b3;
}



