
@charset "UTF-8";
/*
 * 基础色
 */
.cat-loading-fade-enter-active {
  -webkit-animation: fade-in 1s;
          animation: fade-in 1s;
}
.cat-loading-fade-leave-active {
  -webkit-animation: fade-out 1s;
          animation: fade-out 1s;
}
@-webkit-keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fade-out {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes fade-out {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.cat-loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 3px;
  transition: all .2s linear;
  z-index: 999;
}
.cat-loading-loading {
    background-color: #3da8f5;
}
.cat-loading-success {
    background-color: #00B281;
}
.cat-loading-error {
    background-color: #d71345;
}
.cat-loading .cat-icon {
    position: fixed;
    top: 5px;
    right: 5px;
    color: #3da8f5;
}



