
@charset "UTF-8";
/*
 * 基础色
 */
.cat-sidebar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 280px;
  background-color: #383838;
  box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.16);
}
.cat-sidebar .account-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100%;
    height: 120px;
    font-size: 13px;
    padding: 10px 20px;
    background-color: #fff;
}
.cat-sidebar .account-info .avatar {
      height: 64px;
      width: 64px;
      border-radius: 3px;
      background-color: #a6a6a6;
}
.cat-sidebar .account-info .avatar-placeholder {
        color: #fff;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        cursor: default;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
}
.cat-sidebar .account-info .user-info {
      margin-left: 10px;
}
.cat-sidebar .account-info .login-group {
      margin-top: 10px;
}
.cat-sidebar .account-info .login-group .logout-btn {
        color: #a6a6a6;
}
.cat-sidebar .account-info .account {
      font-size: 14px;
}
.cat-sidebar .nav {
    margin-top: 100px;
    padding-right: 80px;
    text-align: right;
    color: #fff;
}
.cat-sidebar .nav a {
      position: relative;
      color: #fff;
      font-size: 20px;
      display: block;
      text-decoration: none;
      margin-bottom: 60px;
      transition: all .3s;
}
.cat-sidebar .nav a::after {
        content: '';
        position: absolute;
        right: -20px;
        top: -10px;
        height: 60px;
        width: 1px;
        background-color: #fff;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        transition: all .3s;
}
.cat-sidebar .nav a:hover {
        color: #3da8f5;
}
.cat-sidebar .nav a:hover::after {
          background-color: #3da8f5;
}
.cat-sidebar .nav a.router-link-active {
        color: #f05b72;
}
.cat-sidebar .nav a.router-link-active::after {
          background-color: #f05b72;
}



