
@charset "UTF-8";
/*
 * 基础色
 */
.cat-btn {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  white-space: nowrap;
  border-radius: 3px;
  color: #666;
  vertical-align: middle;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  color: rgba(0, 0, 0, 0.65);
  outline: none;
  box-shadow: none;
  cursor: pointer;
  transition: all .2s;
}
.cat-btn:hover {
    border-color: #999999;
}
.cat-btn:not(.disabled):active {
    border-color: silver;
}
.cat-btn.disabled {
    opacity: .3;
    cursor: not-allowed;
}
.cat-btn-sm {
  padding: 0 12px;
  font-size: 12px;
  height: 24px;
}
.cat-btn-sm .cat-icon {
    margin: 0 -1px;
    font-size: 14px;
    line-height: 1;
}
.cat-btn-md {
  padding: 0 14px;
  font-size: 14px;
  height: 34px;
}
.cat-btn-md .cat-icon {
    margin: 0 -1px;
    font-size: 16px;
    line-height: 1;
}
.cat-btn-lg {
  padding: 0 16px;
  font-size: 16px;
  height: 44px;
}
.cat-btn-lg .cat-icon {
    margin: 0 -1px;
    font-size: 18px;
    line-height: 1;
}



